<template>
  <div class="showlist">
    <div class="sum border-bt">
      <div class="pt-20">Your Work</div>
      <div class="py-10">{{done}} done / {{allWorks}} works</div>
    </div>
    <!--<ul v-show="listShow">
      <li class="list border-bt">第一条记录</li>
      <li class="list border-bt">第2条记录</li>
    </ul>-->
    <el-collapse accordion>
      <el-collapse-item :title=item.title :name=index v-for="(item,index) in list" :key="item.title">
        <!--<div>{{item.title}}</div>-->
        <div class="note-text" :class="{'done':item.isFinish}">{{item.text}}</div>
        <el-button size="mini" icon="more" @click="ShowShade"></el-button>
        <transition-group name="fadeInRight">
          <div class="oprate" v-show="shadeShow" :key="index" @click.self="ShowShade(false)">
            <el-button size="small" icon="check" @click="setDone(item,index)">done</el-button>
            <el-button size="small" icon="edit" @click="edit(item,index)">edit</el-button>
            <el-button size="small" icon="delete" @click="del(item,index)">del</el-button>
          </div>
          <div class="shade" v-show="shadeShow" :key="index"></div>
        </transition-group>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
  import {mapState, mapMutations, mapActions, mapGetters} from 'vuex'
  export default {
    name: 'ShowList',
    data () {
      return {}
    },
    computed: {
      // 使用对象展开运算符将此对象混入到外部对象中
      ...mapState({
        listShow: state => state.listShow,
        done: 'done',
        allWorks (state) {
          return state.list.length
        },
        list: 'list',
        shadeShow: 'shadeShow'
      })
    },
    methods: {
      ...mapMutations({
        editIndex: 'editIndex'
      }),
      ...mapActions({
        ShowShade: 'oprate/SHOW_SHADE',
        setDone: 'oprate/SET_DONE',
        edit: 'oprate/EDIT_ITEM',
        del: 'oprate/DEL_ITEM',
      }),
      ShowShade(s){
        s ? s : true;
        console.log(Boolean(s));
        this.$store.commit('oprate/SHOW_SHADE', s);
      },
      setDone(item, index){
        console.log(item, index);
        //item.isFinish = true;//不能直接更改
        this.$store.commit('oprate/SET_ITEM_DONE', index);
        this.$store.commit('oprate/SET_DONE', '+1');
        this.$store.commit('oprate/SHOW_SHADE', false);
      },
      edit(item, index){
        this.$store.commit('oprate/EDIT_ITEM', true);
        if (item.isFinish) {
          this.$message.error('已完成的不能编辑了喔~');
          return false
        } else {
          this.$store.commit('SHOW_DIALOG', true);
          this.$parent.title = item.title;
          this.$parent.textarea = item.text;
          this.editIndex(index);
        }
      },
      del(index){
        this.$store.commit('oprate/DEL_ITEM', index);
        this.$store.commit('oprate/SHOW_SHADE', false);
      }
    }
  }
</script>

<style src="css/style.css" scoped></style>
<style scoped>
  .fadeInRight-enter-active {
    animation: fadeInRight 0.5s;
    -webkit-animation: fadeInRight 0.5s;
  }

  .fadeInRight-leave-active {
    animation: fadeOutRight 0.5s;
    -webkit-animation: fadeOutRight 0.5s;
  }

  .bottom-fade-enter-active {
    transition: all .3s ease;
  }

  .bottom-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .bottom-fade-enter, .bottom-fade-leave-active {
    transform: translateY(100px);
    opacity: 0;
  }
</style>



